.footer {
  background-color: hsl(221, 25%, 22%);
  height: auto;
}

.footer-links {
  max-width: 1100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  height: 90%;
  padding: 35px 20px;
}
.easyBank {
  margin-bottom: 40px;
}
.footer .footer-link-items {
  display: flex;
  height: 100%;
  flex: 1;
  justify-content: space-around;


}
.footer .socialMedia {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
 
}
.footer .socialMedia .socialIcons a {
  margin-right: 15px;
}
.footer .socialMedia .socialIcons a {
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  transition: 0.3s ease color;
}
.footer .socialMedia .socialIcons a:hover {
  color: hsla(136, 65%, 51%, 0.596);
}
.footer .footer-link-items ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  list-style: none;
}
.footer .footer-link-items .info {
  margin-right: 20px;
}
.footer .footer-link-items ul  li {
  margin-bottom: 10px;
}
.footer .footer-link-items ul li a:hover {
  color: hsla(136, 65%, 51%, 0.596);
}
.footer .footer-link-items ul li a {
  transition: 0.3s ease color;
  color: #fff;
  text-decoration: none;
}

.footer .copyRight {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-end;
  height: 100%;
  justify-content: space-around;
  margin-top: -30px;

}
.footer .copyRight .request-invite{
  background: linear-gradient(to right ,hsl(136, 65%, 51%), hsl(192, 70%, 51%));
  border: none;
  outline: none;
  display: grid;
  width: 150px;
  color: white;
  padding: 15px 20px;
  border-radius: 40px;
  margin: 20px 0;
  cursor: pointer;
}
.footer .copyRight .request-invite:hover{
  opacity: 0.5;
}
.footer .copyRight a {
  text-decoration: none;
}

.footer .copyRight p {
  color: hsl(233, 8%, 62%);
}
@media (max-width: 750px) {
.footer {
  height: auto;
}
.footer-links {
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .footer-link-items {
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0%;
}


.footer .footer-link-items ul {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.footer .footer-link-items ul li {
  margin: 10px 0;
}
.footer .footer-link-items .info {
  margin: 0;
}
.footer .socialMedia {
  align-items: center;
}
.footer .socialMedia .socialIcons {
  padding: 25px 10px;
}
.footer .socialMedia .socialIcons a {
  margin: 10px;
}

.footer .copyRight {
  align-items: center;
  padding: 20px;
}
.footer .copyRight p {
  padding-top: 10px;
  text-align: center;
    width: 100%;
}
}